
import {defineComponent} from "vue";

export default defineComponent({
  name: "DeltaTimeInput",
  props: {
    modelValue: Number
  },
  data() {
    return {
      unixTime: 0 as number,
      hour: 0 as number,
      minute: 0 as number,
    }
  },

  async created() {
    this.startAssignUnixTime()
    this.setUnixTimeToHourMinutes()
  },

  watch: {
    unixTime(newValue) {
      this.$emit('update:modelValue', newValue)
    }
  },

  methods: {
    startAssignUnixTime() {
      if (this.modelValue === undefined) {
        this.unixTime = 0
        return
      }
      this.unixTime = this.modelValue
    },

    setUnixTimeToHourMinutes() {
      let date = new Date(this.unixTime * 1000);
      if (date.getTime() > 86400000) {
        throw 'DeltaTimeInput 안에 주입된 v-model 의 값이 24시간을 초과합니다. time:' + this.unixTime
      }
      this.hour = date.getUTCHours()
      this.minute = date.getUTCMinutes()
    },

    setHourMinutesToUnixTime() {
      let date = new Date(0);
      date.setUTCHours(this.hour)
      date.setUTCMinutes(this.minute)
      this.unixTime = date.getTime() / 1000
    }
  }
})
